@charset "UTF-8";
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

* {
  min-height: 0;
  min-width: 0;
}

.icon-sort-down {
  width: 1em;
  height: 1em;
  fill: #d9d9d9;
}

.icon-sort_up-down {
  width: 1em;
  height: 1em;
  fill: #d9d9d9;
}

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Slider */
/* Icons */
/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover, .slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before, .slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: #fff;
}

.is-active {
  background: #8C8C8C;
  color: #fff;
}

.companies {
  display: flex;
  max-width: 1600px;
  min-width: 320px;
  min-height: 100vh;
  flex-basis: 100%;
  padding: 50px;
  margin: 0 auto;
  justify-content: space-around;
  align-self: center;
  align-content: space-around;
  flex-wrap: wrap;
}

@media only screen and (max-width: 760px) {
  .companies {
    padding: 50px 0 50px;
  }
}

.company {
  flex-basis: 45%;
  box-shadow: 0 5px 30px 10px rgba(0, 0, 0, 0.1);
  min-height: 300px;
  margin: 20px;
  position: relative;
  border-radius: 10px;
  overflow: hidden;
}

@media only screen and (max-width: 920px) {
  .company {
    flex-basis: 100%;
    min-height: 400px;
  }
}

.company_top-line {
  width: 100%;
  padding: 10px;
  position: relative;
  background: #00b0ff;
  color: #d9d9d9;
}

@media only screen and (max-width: 760px) {
  .company_top-line {
    text-align: center;
  }
}

.company_title {
  display: inline-block;
}

.company_list {
  width: 95%;
  min-height: 250px;
  max-height: 250px;
  margin: 10px auto;
  border-radius: 5px;
  overflow-y: auto;
  overflow-x: hidden;
}

@media only screen and (max-width: 920px) {
  .company_list {
    max-height: 350px;
  }
}

.company_list-item {
  width: 96%;
  margin: 15px;
  padding: 10px;
  font-family: "Raleway", sans-serif;
  border-radius: 3px;
}

.company_list-item:hover {
  background: #00b0ff;
  color: #d9d9d9;
  cursor: pointer;
}

.total-comp_counter {
  display: block;
  width: 150px;
  height: 150px;
  position: absolute;
  top: 58%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 5px solid #d9d9d9;
  border-radius: 50%;
  text-align: center;
  line-height: 147px;
  font-weight: bold;
  font-size: 26px;
  color: #787878;
}

.partners {
  max-width: 1550px;
  flex-basis: 95%;
  display: none;
}

.partners_list-wrap {
  max-width: 98%;
  position: relative;
  min-height: 400px;
  margin: 10px auto;
  overflow-x: auto;
}

.partners_list-wrap:after {
  content: " ";
  display: table;
  clear: both;
}

.partners_list {
  display: flex;
  width: 100%;
  max-width: 200%;
  height: 100%;
  padding-bottom: 20px;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
}

.partners_item {
  position: relative;
  min-width: 100px;
  max-width: 100px;
  height: 100%;
  border-right: 1px solid #8C8C8C;
  white-space: nowrap;
  text-align: center;
}

.partners_name {
  position: relative;
  top: 50%;
  left: 50%;
  color: #d9d9d9;
  transform: rotate(-90deg) translate(-120%, -150%);
  font-size: 22px;
  text-align: right;
}

.partners_count {
  position: absolute;
  top: 10%;
  left: 50%;
  color: #d9d9d9;
  transform: translate(-50%, -10%);
  font-size: 26px;
}

.sort {
  min-width: 20%;
  position: absolute;
  top: 17px;
  right: 15px;
}

.sort:after {
  content: " ";
  display: table;
  clear: both;
}

@media only screen and (max-width: 760px) {
  .sort {
    display: block;
    width: auto;
    position: static;
    margin-top: 10px;
  }
}

@media only screen and (max-width: 760px) and (max-width: 390px) {
  .sort {
    font-size: 14px;
  }
}

.sort_arrow {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

@media only screen and (max-width: 390px) {
  .sort_arrow {
    width: 12px;
    height: 12px;
  }
}

.sort_arrow:hover {
  cursor: pointer;
}

.sort_title {
  float: left;
  vertical-align: top;
}

@media only screen and (max-width: 760px) {
  .sort_title {
    float: left;
  }
}

.sort_type {
  float: right;
  margin-left: 10px;
}

@media only screen and (max-width: 760px) {
  .sort_type {
    float: left;
  }
}

.sort_order {
  display: inline-block;
}

.sort_order span {
  vertical-align: top;
}

.back-arrow {
  width: 24px;
  height: 24px;
  float: right;
  margin: 5px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

@media only screen and (max-width: 450px) {
  .back-arrow {
    float: none;
    margin: 10px auto 0;
  }
}

.back-arrow:hover {
  cursor: pointer;
}

.loc_list {
  display: none;
  max-height: 320px;
}

.news_slider {
  width: 95%;
  height: 70%;
  margin: 15px auto;
}

@media only screen and (max-width: 920px) {
  .news_slider {
    min-height: 350px;
  }
}

.news_item {
  height: 100%;
  padding: 10px;
}

.news_photo {
  width: 100px;
  height: 100px;
  float: left;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: 50% 50%;
}

.news_textual {
  max-height: 150px;
  overflow-y: auto;
}

@media only screen and (max-width: 920px) {
  .news_textual {
    max-height: 300px;
  }
}

.news_meta {
  display: inline-block;
  min-width: 30%;
  clear: both;
  margin-top: 10px;
  font-size: 0;
}

@media only screen and (max-width: 460px) {
  .news_meta {
    min-width: 40%;
  }
}

.news_meta span {
  font-size: 14px;
  margin: 5px;
}

.news_meta-title {
  font-weight: bold;
}

.news .slick-dots li button:before {
  font-size: 24px;
}

.form_wrap {
  width: 40%;
  min-width: 320px;
  margin: 50px auto 0;
  padding: 20px;
  background: #59ccff;
  color: #fff;
  border-radius: 5px;
}

.form_wrap-title {
  text-align: center;
  margin-bottom: 20px;
  cursor: default;
}

.form_wrap .sign {
  width: 100%;
  font-family: "Raleway", sans-serif;
}

.form_wrap .sign-input {
  width: 100%;
  height: 40px;
  margin: 15px 0;
  padding-left: 15px;
  border-radius: 5px;
  color: #8C8C8C;
}

.form_wrap .sign-input::placeholder {
  color: #8C8C8C;
}

.form_wrap .sign-select {
  display: block;
  width: 15%;
  height: 40px;
  margin: 15px 0;
  padding-left: 10px;
  border: none;
  border-radius: 5px;
  font-family: inherit;
  color: #8C8C8C;
}

@media only screen and (max-width: 1300px) {
  .form_wrap .sign-select {
    width: 30%;
  }
}

.form_wrap .sign_check-wrap {
  position: relative;
}

.form_wrap .sign-check {
  display: block;
  margin: 10px 0;
  vertical-align: middle;
}

.form_wrap .sign-check-cond {
  position: absolute;
  top: -5px;
  left: 30px;
}

.form_wrap .sign-cond {
  margin-left: 5px;
}

.form_wrap .sign-submit {
  display: block;
  width: 120px;
  height: 40px;
  margin: 15px auto;
  background: #fff;
  font-size: 18px;
  color: #8C8C8C;
  border-radius: 5px;
}

.form_wrap .sign-submit:hover {
  background: #d9d9d9;
  color: #fff;
}

.serv-resp {
  display: none;
  width: 40%;
  min-width: 320px;
  margin: 20px auto 0;
  background: #d9d9d9;
  color: #fff;
  border-radius: 5px;
  text-align: center;
}
